/* 마이페이지 전체 컨테이너 */
.mypage-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f9f9f9;
    font-family: Arial, sans-serif;
  }
  
  /* 카드 스타일 */
  .mypage-card {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 500px;
    padding: 20px;
    text-align: center;
  }
  
  /* 카드 타이틀 */
  .mypage-title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
  }
  
  /* 컨텐츠 영역 */
  .mypage-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }
  
  /* 사진 섹션 */
  .mypage-photo {
    width: 120px;
    height: 120px;
    border: 1px dashed #ddd;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    background-color: #f5f5f5;
  }
  
  .photo-preview {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .photo-add {
    font-size: 0.9rem;
    color: #3f80f7;
    cursor: pointer;
  }
  
  .photo-add input {
    display: none;
  }
  
  /* 정보 섹션 */
  .mypage-info {
    flex: 1;
    text-align: left;
  }
  
  .mypage-info p {
    margin: 10px 0;
    font-size: 1rem;
    color: #555;
  }
  
  .mypage-info input {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 0.9rem;
  }
  
  /* 버튼 그룹 */
  .button-group {
    display: flex;
    gap: 10px;
    margin-top: 10px;
  }
  
  button {
    padding: 8px 15px;
    background-color: #3f80f7;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #2c6acb;
  }
  
  button:nth-child(2) {
    background-color: #d6d6d6;
    color: #333;
  }
  
  button:nth-child(2):hover {
    background-color: #b5b5b5;
  }
  
  /* 비밀번호 확인 모달 */
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content {
    background: white;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    width: 300px;
  }
  
  .modal-content input {
    width: 100%;
    padding: 8px;
    margin-top: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 1rem;
  }
  
  .modal-content h3 {
    margin-bottom: 10px;
    color: #333;
    font-size: 1.2rem;
    font-weight: bold;
  }